import React from 'react'
import PropTypes from 'prop-types'
import { useTheme, createUseStyles } from 'react-jss'
import { getRadioStyles } from 'frames-jss'
import cx from 'classnames'

const useStyles = createUseStyles((theme) => getRadioStyles(theme))

const Radio = ({ name, className, leftLabel, rightLabel, checked, disabled, invalid }) => {
  const theme = useTheme()
  const styles = useStyles(theme)

  return (
    <label className={cx(styles.radio, className, invalid && 'invalid')}>
      {leftLabel && <span className={styles.radioLabel}>{leftLabel}</span>}
      <input type="radio" name={name} defaultChecked={checked} disabled={disabled} />
      <span className={styles.radioToggle}></span>
      {rightLabel && <span className={styles.radioLabel}>{rightLabel}</span>}
    </label>
  )
}

Radio.propTypes = {
  name: PropTypes.string,
  className: PropTypes.string,
  leftLabel: PropTypes.string,
  rightLabel: PropTypes.string,
  checked: PropTypes.bool,
  disabled: PropTypes.bool,
  invalid: PropTypes.bool,
}
Radio.defaultProps = {
  name: 'radio',
  leftLabel: '',
  rightLabel: '',
  checked: false,
  disabled: false,
  invalid: false,
}

export default Radio
