import React from 'react'
import { useTheme, createUseStyles, ThemeProvider } from 'react-jss'
import cx from 'classnames'

import Layout from '../../components/Layout'
import Main from '../../components/Main'
import SEO from '../../components/SEO'
import Button from '../../components/ui/Button'
import Checkbox from '../../components/ui/Checkbox'
import Radio from '../../components/ui/Radio'
import Switch from '../../components/ui/Switch'
import Slider from '../../components/ui/Slider'

const useStyles = createUseStyles({
  main: {
    maxWidth: 936,
    margin: 'auto',
  },
  callout: {
    padding: 24,
    backgroundColor: ({ initial: theme }) => theme.colors.grayscale[100],
    borderRadius: 6,
    '& h1': {
      marginTop: 0,
      marginBottom: 16,
    },
    '& p': {
      margin: 0,
    },
  },
  section: {
    margin: [0, 0, 40, 0],
    '& h2': {
      margin: [32, 0],
    },
  },
  preview: {
    extend: 'callout',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    flexWrap: 'wrap',
    minHeight: 238,
    border: ({ initial: theme }) => `2px solid ${theme.colors.grayscale[200]}`,
    backgroundColor: ({ updated: theme }) => theme.colors.brand.background,
    '& .row': {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    },
    '& .component': {
      marginRight: 20,
    },
  },
})

const ThemePreview = ({ location }) => {
  const initialTheme = useTheme()
  const updatedTheme = location.state?.theme || initialTheme
  const theme = { initial: initialTheme, updated: updatedTheme }
  const styles = useStyles(theme)

  return (
    <Layout>
      <SEO title="Theme Preview" />
      <Main className={styles.main}>
        <section className={styles.callout}>
          <h1>Preview</h1>
          <p>Here you can download custumized components.</p>
        </section>
        <section className={styles.section}>
          <h2>Button</h2>
          <ThemeProvider theme={theme.updated}>
            <div className={styles.preview}>
              {['default', 'secondary'].map((type) => (
                <div key={type} className="row">
                  {['primary', 'positive', 'negative', 'warning'].map((context) => (
                    <Button key={`${type}-${context}`} className={cx(context, type, 'component')}>
                      Button
                    </Button>
                  ))}
                </div>
              ))}
            </div>
          </ThemeProvider>
        </section>
        <section className={styles.section}>
          <h2>Checkbox</h2>
          <ThemeProvider theme={theme.updated}>
            <div className={styles.preview}>
              {[
                {},
                { checked: true },
                { disabled: true },
                { checked: true, disabled: true },
                { invalid: true },
                { checked: true, invalid: true },
                { checked: true, invalid: true, disabled: true },
                { leftLabel: 'Label' },
                { rightLabel: 'Label' },
              ].map((state, idx) => (
                <Checkbox key={idx} className="component" {...state} />
              ))}
            </div>
          </ThemeProvider>
        </section>
        <section className={styles.section}>
          <h2>Radio</h2>
          <ThemeProvider theme={theme.updated}>
            <div className={styles.preview}>
              {[
                {},
                { checked: true },
                { name: 'radio-disabled', disabled: true },
                { name: 'radio-disabled', disabled: true, checked: true },
                { name: 'radio-invalid', invalid: true },
                { name: 'radio-invalid', invalid: true, checked: true },
                { name: 'radio-invalid', invalid: true, checked: true, disabled: true },
                { name: 'radio-label', leftLabel: 'Label' },
                { name: 'radio-label', rightLabel: 'Label' },
              ].map((state, idx) => (
                <Radio key={idx} className="component" {...state} />
              ))}
            </div>
          </ThemeProvider>
        </section>
        <section className={styles.section}>
          <h2>Switch</h2>
          <ThemeProvider theme={theme.updated}>
            <div className={styles.preview}>
              {[
                {},
                { checked: true },
                { disabled: true },
                { disabled: true, checked: true },
                { invalid: true },
                { invalid: true, checked: true },
                { invalid: true, checked: true, disabled: true },
                { leftLabel: 'Label' },
                { rightLabel: 'Label' },
              ].map((state, idx) => (
                <Switch key={idx} className="component" {...state} />
              ))}
            </div>
          </ThemeProvider>
        </section>
        <section className={styles.section}>
          <h2>Slider</h2>
          <ThemeProvider theme={theme.updated}>
            <div className={styles.preview}>
              {[{}, { disabled: true }, { invalid: true }, { invalid: true, disabled: true }].map(
                (state, idx) => (
                  <Slider key={idx} className="component" {...state} />
                ),
              )}
            </div>
          </ThemeProvider>
        </section>
      </Main>
    </Layout>
  )
}

export default ThemePreview
