import React, { useEffect } from 'react'
import PropTypes from 'prop-types'
import { useTheme, createUseStyles } from 'react-jss'
import { getSliderStyles, setSliderProgress } from 'frames-jss'
import cx from 'classnames'

const useStyles = createUseStyles((theme) => getSliderStyles(theme))

const Slider = ({ value, className, disabled, invalid }) => {
  const theme = useTheme()
  const styles = useStyles(theme)

  useEffect(() => {
    document
      .querySelectorAll('input[type="range"]')
      .forEach((input) => void setSliderProgress({ target: input }))
  }, [])

  return (
    <label className={cx(styles.slider, className)}>
      <input
        type="range"
        defaultValue={value}
        disabled={disabled}
        data-invalid={invalid}
        onInput={setSliderProgress}
      />
      <span className="progress"></span>
      <span className="track"></span>
    </label>
  )
}

Slider.propTypes = {
  className: PropTypes.string,
  value: PropTypes.number,
  disabled: PropTypes.bool,
  invalid: PropTypes.bool,
}
Slider.defaultProps = {
  value: 50,
  disabled: false,
  invalid: false,
}

export default Slider
