import React from 'react'
import PropTypes from 'prop-types'
import { useTheme, createUseStyles } from 'react-jss'
import { getCheckboxStyles } from 'frames-jss'
import cx from 'classnames'

const useStyles = createUseStyles((theme) => getCheckboxStyles(theme))

const Checkbox = ({ className, checked, disabled, invalid, leftLabel, rightLabel }) => {
  const theme = useTheme()
  const styles = useStyles(theme)

  return (
    <label className={cx(styles.checkbox, className, invalid && 'invalid')}>
      {leftLabel && <span className={styles.checkboxLabel}>{leftLabel}</span>}
      <input type="checkbox" disabled={disabled} defaultChecked={checked} />
      <span className={styles.checkboxToggle}></span>
      {rightLabel && <span className={styles.checkboxLabel}>{rightLabel}</span>}
    </label>
  )
}

Checkbox.propTypes = {
  className: PropTypes.string,
  checked: PropTypes.bool,
  disabled: PropTypes.bool,
  invalid: PropTypes.bool,
  leftLabel: PropTypes.string,
  rightLabel: PropTypes.string,
}
Checkbox.defaultProps = {
  checked: false,
  disabled: false,
  invalid: false,
}

export default Checkbox
