import React from 'react'
import PropTypes from 'prop-types'
import { useTheme, createUseStyles } from 'react-jss'
import { getSwitchStyles } from 'frames-jss'
import cx from 'classnames'

const useStyles = createUseStyles((theme) => getSwitchStyles(theme))

const Switch = ({ className, leftLabel, rightLabel, checked, disabled, hover, focus, invalid }) => {
  const theme = useTheme()
  const styles = useStyles(theme)

  return (
    <label className={cx(styles.switch, className, invalid && 'invalid')}>
      {leftLabel && <span className="label">{leftLabel}</span>}
      <input type="checkbox" defaultChecked={checked} disabled={disabled} />
      <span className="toggle"></span>
      {rightLabel && <span className="label">{rightLabel}</span>}
    </label>
  )
}

Switch.propTypes = {
  className: PropTypes.string,
  leftLabel: PropTypes.string,
  rightLabel: PropTypes.string,
  checked: PropTypes.bool,
  disabled: PropTypes.bool,
  hover: PropTypes.bool,
  focus: PropTypes.bool,
  invalid: PropTypes.bool,
}
Switch.defaultProps = {
  leftLabel: '',
  rightLabel: '',
  checked: false,
  disabled: false,
  hover: false,
  focus: false,
  invalid: false,
}

export default Switch
